// @ts-nocheck
import React, { useState, useEffect, useRef} from "react";
import { useDropzone } from "react-dropzone";
import Gallery from "react-photo-gallery";
import { Streamlit, withStreamlitConnection } from "streamlit-component-lib";
import {getImageDataFromBase64URI} from "./Utils";
import "./main.css";

const ImageUploader = ({args}) => {
  const [files, setFiles] = useState<any[]>([]);
  const galleryPhotos = useRef<any[]>([]);

  useEffect(() => Streamlit.setFrameHeight());

  async function onSelectBlob(blob, origin) {
    const reader = new FileReader();
    reader.onloadend = async () => {
      const base64url: any = reader.result;
      setFiles([base64url]);
      const data = await getImageDataFromBase64URI(base64url, args.maxPixels);
      Streamlit.setComponentValue({
        "origin": origin,
        ...data
      });
    };
    reader.readAsDataURL(blob);
  };

  const handleSelectPictureFromURL = async (url: string, origin: string) => {
    const data = await fetch(url);
    const blob = await data.blob();
    onSelectBlob(blob, origin);
  };

  useEffect(() => {
    if (!args.initialDefaultPicture || files.length) {
      return;
    }
    // already in a base64 format - also no need to notify python as it's already
    // the default return value of the component
    if (args.initialDefaultPicture.startsWith("data:")) {
      setFiles([args.initialDefaultPicture]);
    } else {
      handleSelectPictureFromURL(args.initialDefaultPicture, "default");
    }
  // eslint-disable-next-line
  }, [args.initialDefaultPicture, files]);
  useEffect(() => {
    galleryPhotos.current = [];
    args.presetImages.forEach(async function(imageUrl: string, imageNum: number) {
      const data = await fetch(imageUrl);
      const blob = await data.blob();
      const reader = new FileReader();
      reader.onloadend = async () => {
        const base64url: any = reader.result;
        const data = await getImageDataFromBase64URI(base64url, args.maxPixels);
        const photo = {
          width: data.w,
          height: data.h,
          src: imageUrl,
          key: `image-idx-${galleryPhotos.current.length}`,
          order: imageNum,
        };
        galleryPhotos.current.push(photo);
        galleryPhotos.current.sort((a: any, b: any) => a.order - b.order);
      };
      reader.readAsDataURL(blob);
    });
  // NB: Streamlit might change the `args.presetImages` to a different object
  // with the same value. Let's only recalculate when its value changes
  // eslint-disable-next-line
  }, [JSON.stringify(args.presetImages), args.maxPixels]);


  const { getRootProps, getInputProps, } = useDropzone({
    maxFiles: 1,
    accept: {
      "image/*": [".jpeg", ".png"],
    },
    onDrop: (acceptedFiles) => {
      console.assert(acceptedFiles.length === 1);
      onSelectBlob(acceptedFiles[0], "upload");
    },
  });

  const thumbs = files.map((file) => (
    <div key={0} className="img-container">
      <img
        alt="streamlitImg"
        src={(file && file.preview) || file}
        // Revoke data uri after image is loaded
        onLoad={() => {
          URL.revokeObjectURL(file.preview || file);
        }}
      />
    </div>
  ));

  const handleGalleryClick = async (e: any) => {
    handleSelectPictureFromURL(e.target.src, "gallery");
  };

  useEffect(() => {
    // Make sure to revoke the data uris to avoid memory leaks, will run on unmount
    return () => files.forEach((file) => URL.revokeObjectURL(file.preview));
  }, [files]);
  return (
    <div className="upload-container">
      <div {...getRootProps()}>
        <input {...getInputProps()} />
        <div className="upload-box">
          <b>Instructions</b>Drag or Click to Upload Image
        </div>
        {files.length ? (
          <>
            <h2>Preview</h2>
            <aside>{thumbs}</aside>
          </>
        ) : (
          ""
        )}
      </div>
      <div className="gallary">
        {/*  @ts-ignore */}
        <Gallery direction="row" photos={galleryPhotos.current} onClick={handleGalleryClick} />
      </div>
    </div>
  );
};

export default withStreamlitConnection(ImageUploader);
// export default ImageUploader;
