const DATA_STARTS_WITH = "data:";
const B64_STARTS_AFTER = ";base64,";

function getImageElementFromURI(imageURI: string): Promise<HTMLImageElement> {
    return new Promise(function(resolve, reject) {
        var imgElement = document.createElement("img");
        imgElement.crossOrigin = "anonymous";
        imgElement.src = imageURI;
        imgElement.onload = function() {
            resolve(imgElement);
        };
    });
}

function getImageDataFromBase64URIWithoutDimensions(imageURI: string) {
    const b64start = imageURI.search(B64_STARTS_AFTER);
    if (b64start === -1) {
        return null;
    }
    const b64 = imageURI.slice(b64start + B64_STARTS_AFTER.length);
    console.assert(imageURI.startsWith(DATA_STARTS_WITH));
    const dataType = imageURI.slice(DATA_STARTS_WITH.length, b64start);
    return {
        dataType: dataType,
        b64: b64,
    };
}

export async function getImageDataFromBase64URI(imageURI: string, maxPixels: number) {
    const imgElement = await getImageElementFromURI(imageURI);
    return {
        ...getImageDataFromElement(imgElement, maxPixels),
        url: undefined,
    };
}

export function getImageDataFromElement(imgElement: HTMLImageElement, maxPixels: number = 512*512) {
    // Format: data:<contentType>;base64,<b64>

    var c = document.createElement("canvas");
    var width = imgElement.naturalWidth;
    var height = imgElement.naturalHeight;
    const numPixels = width * height;
    if (numPixels > maxPixels) {
        const ratio = Math.sqrt(maxPixels / numPixels);
        width = width * ratio;
        height = height * ratio;
    }
    c.width = width;
    c.height = height;

    var ctx = (c.getContext("2d"))!;
    ctx.drawImage(imgElement, 0, 0, width, height);
    const data = c.toDataURL("image/jpeg", 1.0); // get rid of Alpha channel (otherwise exports as PNG RGBA)
    return {
        ...getImageDataFromBase64URIWithoutDimensions(data),
        w: imgElement.naturalWidth,
        h: imgElement.naturalHeight,
        url: imgElement.src,
    }
}

export function getImageURIFromData(image_data: any): string {
    return "data:" + image_data.dataType + ";base64," + image_data.b64;
}
